@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Montserrat", BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  letter-spacing: 1px;
  overflow: hidden;
  font-size: 62.5%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}.access-body{margin-top:50px}.main-container{width:400px;margin:1% auto}.form-container{bottom:10px}legend.form-label{font-size:1.5rem;color:#3d658f}.control-label{font-size:0.8rem;font-weight:bold;color:#52657a}.form-control{font-size:1rem}.form-hint{font-size:0.6rem;line-height:1.4;margin:-5px auto 5px;color:#999}.form-hint.error{color:#c00;font-size:0.8rem}button.btn{letter-spacing:1px;font-size:0.8rem;font-weight:600}.password-count{bottom:16px;right:10px;font-size:1rem}.strength-meter{position:relative;height:3px;background:#ddd;margin:7px 0;border-radius:2px}.strength-meter:before,.strength-meter:after{content:"";height:inherit;background:transparent;display:block;border-color:#fff;border-style:solid;border-width:0 6px 0;position:absolute;width:calc(20% + 6px);z-index:10}.strength-meter:before{left:calc(20% - 3px)}.strength-meter:after{right:calc(20% - 3px)}.strength-meter-fill{background:transparent;height:inherit;position:absolute;width:0;border-radius:inherit;transition:width 0.5s ease-in-out, background 0.25s}.strength-meter-fill[data-strength="0"]{width:20%;background:darkred}.strength-meter-fill[data-strength="1"]{width:40%;background:#ff4500}.strength-meter-fill[data-strength="2"]{width:60%;background:orange}.strength-meter-fill[data-strength="3"]{width:80%;background:#9acd32}.strength-meter-fill[data-strength="4"]{width:100%;background:green}

